<template>

  <v-card >
          <v-dialog v-model="elModal" max-width="500">
                                    <v-card>
                                        <v-list-item two-line>
                                        <v-list-item-content>
                                            <v-list-item-title class="text-h6">
                                            DATOS DE LA FACTURA QUE SERA ANULADA
                                            </v-list-item-title>
                                        </v-list-item-content>
                                        </v-list-item>
                                        <v-list class="transparent">
                                        <v-list-item>
                                            <v-list-item-title>FACTURA:</v-list-item-title>
                                            <v-list-item-subtitle class="text-right">
                                            {{factura}}
                                            </v-list-item-subtitle>
                                        </v-list-item>
                                         <v-list-item>
                                            <v-list-item-title>FECHA:</v-list-item-title>
                                            <v-list-item-subtitle class="text-right">
                                           {{fecha}}
                                            </v-list-item-subtitle>
                                        </v-list-item>
                                        <v-list-item>
                                            <v-list-item-title>CLIENTE:</v-list-item-title>
                                            <v-list-item-subtitle class="text-right">
                                            {{cliente}}
                                            </v-list-item-subtitle>
                                        </v-list-item>
                                         <v-list-item>
                                            <v-list-item-title>NIT CLIENTE:</v-list-item-title>
                                            <v-list-item-subtitle class="text-right">
                                           {{descripcion}}
                                            </v-list-item-subtitle>
                                        </v-list-item>
                                        <v-list-item>
                                            <v-list-item-title>TOTAL:</v-list-item-title>
                                            <v-list-item-subtitle class="text-right">
                                           {{monto}}
                                            </v-list-item-subtitle>
                                        </v-list-item>
                                        
                                        </v-list>

                                        <v-divider></v-divider>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                                <v-btn  @click.native="confirmareliminacion" color="info" dark>
                                                            <v-icon dark left>mdi-archive-off-outline</v-icon>ANULAR
                                                </v-btn>
                                                <v-btn 
                                                color="red darken-1" dark
                                                 @click="close"
                                                >
                                                    <v-icon dark left>mdi-cancel</v-icon>CANCELAR
                                                </v-btn>
                                        
                                        </v-card-actions>

                                    </v-card>
                                                        </v-dialog>
                                                        <v-dialog v-model="dialogDelete" max-width="500px">
                                                        <v-card>
                                                            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                                                            <v-card-actions>
                                                            <v-spacer></v-spacer>
                                                            <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                                                            <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                                                            <v-spacer></v-spacer>
                                                            </v-card-actions>
                                                        </v-card>
                                                        </v-dialog>         
    <v-card-title >

     <p> ANULACION DE FACTURA</p>
     
      </v-card-title>
       
      <v-spacer></v-spacer>
      <v-card-title >
      
                                <v-flex xs2 sm2 md2 lg2 xl2>
                                     <v-text-field type= "date" :rules="fechadelrules" v-model="fecha_del" label="FECHA INICIAL"> </v-text-field> 
                                     
                                </v-flex>
                                 <v-flex xs1 sm1 md1 lg1 xl1>
                                    
                                 </v-flex>
                                 <v-flex xs2 sm2 md2 lg2 xl2>
                                    <v-text-field type= "date" :rules="fechaalrules" v-model="fecha_al" label="FECHA FINAL"></v-text-field> 
                                 </v-flex>
                                 <v-flex xs2 sm2 md2 lg2 xl2> </v-flex>                      
                                 
                                 <div class="text-center">
                                       <v-btn class="ma-2" @click="procesar()" color="info" dark>
                                        
                                        <v-icon dark left>mdi-clipboard-search-outline</v-icon>GENERAR INFORME
                                    </v-btn>
                                
                                       <v-btn  class="ma-2" @click="downloadExl" color="success" dark>
                                        
                                        <v-icon dark left>mdi-file-download</v-icon>DESCARGAR XLS
                                    </v-btn>
                                 </div>
    </v-card-title>
                                  <v-card-title>
                                    <v-text-field
                                        v-model="search"
                                        append-icon="mdi-magnify"
                                        label="BUSCAR DETALLE"
                                        single-line
                                        hide-details
                                    ></v-text-field>
                                    </v-card-title>
                                <v-data-table
                                :headers="headers"
                                :items="datos"
                                :search="search"
                                class="elevation-1"
                                id="tableId"
                                >
                                         
                                            
                                 <template v-slot:items="props">
                                         <td>{{ props.item.numero }}</td>
                                          <td>{{ props.item.fecha}} </td>
                                        <td>{{ props.item.numero_factura }}</td>
                                        <td>{{ props.item.nombref }}</td>
                                        <td>{{ props.item.nitf}}</td>
                                        <td>{{ props.item.paterno}} </td>
                                         <td>{{ props.item.materno}} </td>
                                          <td>{{ props.item.nombres}} </td>
                                          <td>{{ props.item.items}} </td>
                                        <td>{{ props.item.total}}</td>
                                        
                                                                       
                                    </template>
                                     <template #[`item.actions`]="{item}">
                                        <v-icon
                                            small
                                            class="mr-2"
                                             @click="eliminarItem(item)"
                                        >
                                            mdi-archive-off-outline
                                        </v-icon>
                                        <v-icon
                                            small
                                            @click="deleteItem(item)"
                                        >
                                            mdi-delete
                                        </v-icon>
                                        </template>
                                
                                </v-data-table>
                                
                               <v-alert  text  v-model ="alert.show" :type="alert.type" dismissible>{{alert.message}}</v-alert>
                                
                               
                                
                               
  
  </v-card>                      
</template>
<script>
import  axios from 'axios';
var XLSX = require('xlsx')
var FileSaver = require('file-saver')

  export default {
    data () {
      return {
                search: '',
                fecha_del:'',
                fecha_al:'',
                datos:[],
                elModal: 0,
                alert:{show:false , type:'',message:'' },
                fechadelrules:[
                         value =>!!value || 'INGRESE UNA FECHA',
                        // value =>(value && value.lemgth>=4 || 'EL USUARIO ES MUY CORTO') 
                          ],
                fechaalrules:[
                         value =>!!value || 'INGRESE UNA FECHA',
                        // value =>(value && value.lemgth>4 || 'LA CONTRASEÑA ES MUY CORTA')          
                          ],
                //fechas:{fecha_del:'',fecha_al:''}, 
                headers: [
                { text: 'NRO', value: 'numero' , sortable: false  },
                            { text: 'FECHA', value: 'fecha' , sortable: true  },
                            { text: 'FACTURA', value: 'numero_factura' , sortable: true  },
                            { text: 'NOMBRE CLIENTE', value: 'nombref', sortable: false  },
                            { text: 'NIT', value: 'nitf', sortable: false  },
                            { text: 'PATERNO', value: 'paterno', sortable: false  },
                            { text: 'MANTERNO', value: 'materno', sortable: false  },
                            { text: 'NOMBRES', value: 'nombres', sortable: false  },
                            { text: 'ITEMS', value: 'items', sortable: false  },
                            { text: 'TOTAL(Bs)', value: 'total', sortable: false  },
                            { text: 'OPCIONES', value: 'actions', sortable: false },
                ]
            
                
            }
            
            },
    computed:{
                
    },
    methods:{
       procesar(){
               // this.fecha=this.fecha_del;
                this.listar(this.fecha_del,this.fecha_al); 
            },

            async listar(fecha_del,fecha_al){
                let me=this;
                 await  axios.get('/anulado/anufactura/'+this.fecha_del+','+this.fecha_al).then(function(response){
                    
                     if (response.data.resultado==null){
                         me.datos=[];
                  me.alert={show: true,type:'error', message:'NO EXISTEN DATOS:('};
                } 
                else {//console.log(response.data);
                    me.datos=response.data.resultado;
                     me.alert={show:false,type:'', message:''};
                }
                }).catch(function(error){
                    console.log(error);
                });
            },
            downloadExl() {
                let wb = XLSX.utils.table_to_book(document.getElementById('tableId')),
                    wopts = {
                        bookType: 'xlsx',
                        bookSST: false,
                        type: 'binary'
                    },
                    wbout = XLSX.write(wb, wopts);
 
               FileSaver.saveAs(new Blob([this.s2ab(wbout)], {
                    type: "application/octet-stream;charset=utf-8"
                                 }), "UNICEN-REP.xlsx");
            },
             eliminarItem (item) {
                this.factura=item.numero_factura;
                this.fecha=item.fecha
                this.monto=item.total
                this.cliente=item.nombref
                this.descripcion=item.nitf
                
                //this.editedIndex=1;
                this.elModal=true
             },
              deleteItem (item) {
                this.editedIndex = item.nitf
                this.editedItem = item.nitf
                this.dialogDelete = true
            },
              deleteItemConfirm () {
                 this.desserts.splice(this.editedIndex, 1)
                 this.closeDelete()
                },
                closeDelete () {
                    this.dialogDelete = false
                    this.$nextTick(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                    })
                },
                close () {
                     this.elModal=false
                    //this.$nextTick(() => {
                    //this.editedItem = Object.assign({}, this.defaultItem)
                    //this.editedIndex = -1
                    //})
                },

            s2ab(s) {
                if (typeof ArrayBuffer !== 'undefind') {
                    var buf = new ArrayBuffer(s.length);
                    var view = new Uint8Array(buf);
                    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                } else {
                    var buf = new Array(s.length);
                    for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                }
            }

            
             
      }    
  }
</script>