<template>

  <v-card >
    <v-card-title >
     <h5>DETALLE COBRO SEGURO UNICEN</h5>
     
      </v-card-title>
      <v-spacer></v-spacer>
      <v-card-title >
      
                                <v-flex xs2 sm2 md2 lg2 xl2>
                                     <v-text-field type= "date" :rules="fechadelrules" v-model="fecha_del" label="FECHA INICIAL"> </v-text-field> 
                                     
                                </v-flex>
                                 <v-flex xs1 sm1 md1 lg1 xl1>
                                    
                                 </v-flex>
                                 <v-flex xs2 sm2 md2 lg2 xl2>
                                    <v-text-field type= "date" :rules="fechaalrules" v-model="fecha_al" label="FECHA FINAL"></v-text-field> 
                                 </v-flex>
                                 <v-flex xs2 sm2 md2 lg2 xl2> </v-flex>                      
                                 
                                 <div class="text-center">
                                       
                                        <v-btn
                                            class="mx-2"
                                            fab
                                            dark
                                            x-small
                                            color="info"
                                            @click="procesar()"
                                            title="GENERAR INFORME"
                                            >
                                            <v-icon dark>
                                                mdi-clipboard-search-outline
                                            </v-icon>
                                        </v-btn>
                                          <v-btn
                                            class="mx-2"
                                            fab
                                            dark
                                            x-small
                                            color="success"
                                            @click="downloadExl"
                                            title="DESCARGAR XLS"
                                            >
                                            <v-icon dark>
                                                mdi-file-download
                                            </v-icon>
                                        </v-btn>
                                
                                       
                                 </div>
    </v-card-title>
                                  <v-card-title>
                                    <v-text-field
                                        v-model="search"
                                        append-icon="mdi-magnify"
                                        label="BUSCAR DETALLE"
                                        single-line
                                        hide-details
                                    ></v-text-field>
                                    </v-card-title>
                                <v-data-table
                                :headers="headers"
                                :items="datos"
                                :search="search"
                                id="tableId"
                                >
                                 <template slot="items" slot-scope="props">
                                         <td>{{ props.item.numero }}</td>
                                          <td>{{ props.item.fecha}} </td>
                                        <td>{{ props.item.numero_factura }}</td>
                                         <td>{{ props.item.paterno}} </td>
                                         <td>{{ props.item.materno}} </td>
                                          <td>{{ props.item.nombres}} </td>
                                          <td>{{ props.item.nombresucursal}} </td>
                                          <td>{{ props.item.nombrecaja}} </td>
                                          <td>{{ props.item.espa}} </td>
                                         <td>{{ props.item.esma}} </td>
                                          <td>{{ props.item.esnom}} </td>
                                          <td>{{ props.item.descripcion}} </td>
                                        <td>{{ props.item.monto_cubierto}}</td>      
                                    </template>
                                
                                </v-data-table>
                                
                               <v-alert  text  v-model ="alert.show" :type="alert.type" dismissible>{{alert.message}}</v-alert>
                                
                                <v-row align="center" justify="center">
                                <v-flex xs4 sm4 md4 lg4 xl4>
                                       <h5>TOTAL INGRESOS(Bs):<strong>{{monto_cubierto=(calcularTotalingresosbol).toFixed(2)}} </strong></h5> 
                                </v-flex>
                                 <v-flex xs4 sm4 md4 lg4 xl4>
                                        <h5>TOTAL SEGUROS COBRADOS:<strong>{{segcant=(calcularcantidad)}} </strong></h5> 
                                </v-flex>
                                </v-row>
                                
                               
  
  </v-card>                      
</template>
<script>
import  axios from 'axios';
var XLSX = require('xlsx')
var FileSaver = require('file-saver')

  export default {
    data () {
      return {
                search: '',
                fecha_del:'',
                fecha_al:'',
                segcant:0,
                datos:[],
                alert:{show:false , type:'',message:'' },
                fechadelrules:[
                         value =>!!value || 'INGRESE UNA FECHA',
                        // value =>(value && value.lemgth>=4 || 'EL USUARIO ES MUY CORTO') 
                          ],
                fechaalrules:[
                         value =>!!value || 'INGRESE UNA FECHA',
                        // value =>(value && value.lemgth>4 || 'LA CONTRASEÑA ES MUY CORTA')          
                          ],
                //fechas:{fecha_del:'',fecha_al:''}, 
                headers: [
                            { text: 'FECHA', value: 'fecha' , sortable: true  },
                            { text: 'FACTURA', value: 'numero_factura' , sortable: true  },
                            { text: 'PATERNO', value: 'paterno', sortable: false  },
                            { text: 'MANTERNO', value: 'materno', sortable: false  },
                            { text: 'NOMBRES', value: 'nombres', sortable: false  },
                            { text: 'GESTION', value: 'gestion', sortable: false  },
                            { text: 'ITEM', value: 'item', sortable: false  },
                            { text: 'MONTO', value: 'monto', sortable: false  }
                        
                ]
            
                
            }
            
            },
    computed:{
                calcularTotalingresosbol:function () {
                var resingbol=0.0;
                for(var i=0;i<this.datos.length;i++){
                    //resultado=resultado+(this.detalleprecio[i].cantidad*this.detalleprecio[i].montousd);
                    resingbol=resingbol+(this.datos[i].monto);
                    
                }
                return resingbol;
            },
            calcularcantidad:function () {
                var cantidadseg=0;
                for(var i=0;i<this.datos.length;i++){
                    //resultado=resultado+(this.detalleprecio[i].cantidad*this.detalleprecio[i].montousd);
                    cantidadseg = cantidadseg +1 ;

                }
                return cantidadseg;
            }
    },
    methods:{
       procesar(){
               // this.fecha=this.fecha_del;
                this.listar(this.fecha_del,this.fecha_al); 
            },

            async listar(fecha_del,fecha_al){
                let me=this;
                 await  axios.get('/reporte/segurofec/'+this.fecha_del+','+this.fecha_al).then(function(response){
                    
                     if (response.data.resultado==null){
                         me.datos=[];
                  me.alert={show: true,type:'error', message:'NO EXISTEN DATOS:('};
                } 
                else {//console.log(response.data);
                    me.datos=response.data.resultado;
                     me.alert={show:false,type:'', message:''};
                }
                }).catch(function(error){
                    console.log(error);
                });
            },
            downloadExl() {
                let wb = XLSX.utils.table_to_book(document.getElementById('tableId')),
                    wopts = {
                        bookType: 'xlsx',
                        bookSST: false,
                        type: 'binary'
                    },
                    wbout = XLSX.write(wb, wopts);
 
               FileSaver.saveAs(new Blob([this.s2ab(wbout)], {
                    type: "application/octet-stream;charset=utf-8"
                                 }), "UNICEN-REP.xlsx");
            },
            s2ab(s) {
                if (typeof ArrayBuffer !== 'undefind') {
                    var buf = new ArrayBuffer(s.length);
                    var view = new Uint8Array(buf);
                    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                } else {
                    var buf = new Array(s.length);
                    for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
                    return buf;
                }
            }

            
             
      }    
  }
</script>